body {
    margin: 0;
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
}

footer {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 20vh;
    background-color: rgb(217, 217, 218);
    font-size: 1.2rem;
}

input:hover {
    cursor: pointer;
};